import Vue from 'vue'
import message from './index.vue'

const Constructor = Vue.extend(message)
let index = 0; // instance
let instance = null; // instanceinstancenewinstanceinstance

function generateInstance(options) {
  let instance = new Constructor({
    // 
    propsData: options
  }).$mount(document.createElement('div'))

  if (typeof options.onClose === 'function') {
    instance.onClose = options.onClose;
  }

  let id = `message${index++}`;
  instance.id = id;
  // onClose
  instance.$once('messageClose', function () {
    const curInstance = this;
    if (typeof curInstance.onClose === 'function') {
      curInstance.onClose();
    }
  })

  return instance;
}

const Message = {};
Message.$message = (options = {}) => {
  if (instance) instance.$destroy();
  instance = generateInstance(options);
}
Message.$closeMessage = () => {
  if (instance) instance.$destroy();
}
export default Message
export { Message }