export default {
  data () {
    return {
      visible: false,
    }
  },
  watch: {
    visible(val) {
      if (!val) {
        // 
        this.$nextTick(() => {
          this.$el.addEventListener('transitionend', this.destroyElement)
        })
      }
    }
  },
  mounted() {
    // visible
    this.visible = true
    // $mount
    document.body.appendChild(this.$el);
  },
  destroyed() {
    // 
    this.$el.parentNode.removeChild(this.$el);
  },
  methods: {
    destroyElement() {
      this.destroyed();
    },
    // 
    close() {
      this.visible = false;
    }
  },
}